import { ElMessage } from 'element-plus';
import { toLine } from '@/utils/index';

export const useCopy = (text: string, style: any) => {
  navigator.clipboard
    .writeText(
      `<m-icon${toLine(text)} :style="{fontSize: ${style.fontSize},color: '${
        style.color
      }'}"/>`,
    )
    .then(() => {
      ElMessage({
        message: '已复制到剪贴板!',
        type: 'success',
      });
    })
    .catch((error) => {
      ElMessage({
        message: '复制失败>_<!',
        type: 'error',
      });
    });
};
