import { createApp } from 'vue';
import App from './App.vue';
import './assets/index.scss';
// import '@/assets/coverC.scss';
import router from '@/router/index';
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css';
import * as Icons from '@element-plus/icons-vue';
import { toLine } from '@/utils/index';
import mingUI from '@/components/index';

const app = createApp(App);

// 解决宽度高度导致的「ResizeObserver loop limit exceeded」问题
const debounce = (fn: Function, delay: number) => {
  let timer: number;
  return function () {
    let context = app;
    let args = arguments;
    clearTimeout(timer);
    timer = setTimeout(function () {
      fn.apply(context, args);
    }, delay);
  };
};

const _ResizeObserver = window.ResizeObserver;
window.ResizeObserver = class ResizeObserver extends _ResizeObserver {
  constructor(callback: ResizeObserverCallback) {
    callback = debounce(callback, 16);
    super(callback);
  }
};

// 全局注册图标
// el-icon-xxx
for (const i in Icons) {
  // 注册全局组件
  app.component(`m-icon${toLine(i)}`, (Icons as any)[i]);
}

app.use(mingUI).use(router).use(ElementPlus).mount('#app');
